      <my-dialog :is-show="isShowBuyDialog" @on-close="closeDialog()" >
    <table class="buy-dialog-table">
        <tr>
            <th>购买数量</th>
            <th>行业</th>
            <th>产品版本</th>
            <th>有效时间</th>
            <th>总价</th>
          </tr>
           <tr>
            <td>{{buyNum}}</td>
            <td>{{trade.label}}</td>
            <td>{{meijie}}</td>
            <td>半年</td>
            <td>{{price}}</td>
          </tr>
    </table>
    <!-- <h3 class="buy-dialog-title">请选择银行</h3> -->
 </my-dialog>
    </div>
   
  </div>
</template>

<script>
import VSelection from "../../components/base/selection";
import VCount from "../../components/base/counter";
import VMultChoose from "../../components/base/multiplyChooser";
import Dialog from "../../components/base/dialog";

export default {
  components: { VSelection, VCount, VMultChoose, MyDialog: Dialog },
  computed: {
    meijie() {
      let source = "";
      for (let i = 0; i < this.version.length; i++) {
        if (i === this.version.length - 1) {
          source += this.version[i].label;
        } else {
          source += this.version[i].label + ",";
        }
      }
      return source;
    }
  },
  data() {
    return {
      buyNum: 1,
      tradeList: [
        {
          label: "出版业",
          value: 0
        },
        {
          label: "媒体",
          value: 1
        },
        {
          label: "金融",
          value: 2
        },
        {
          label: "互联网",
          value: 3
        },
        {
          label: "游戏",
          value: 4
        }
      ],
      versionList: [
        {
          label: "初级版",
          value: 0
        },
        {
          label: "中级版",
          value: 1
        },
        {
          label: "高级版",
          value: 2
        },
        {
          label: "专家版",
          value: 3
        }
      ],
      trade: {
        label: "出版业",
        value: 0
      },
      version: {
        label: "初级版",
        value: 0
      },
      price: 100,
      isShowBuyDialog: false
    };
  },
  methods: {
    onParamChange(type, source) {
      this[type] = source;
      this.getPrice();
    },
    getPrice() {
      let newPrice = 100;
      for (let i = 0; i < this.version.length; i++) {
        newPrice += this.version[i].value * 30;
      }
      this.price = (this.trade.value * 20 + newPrice) * this.buyNum;
    },
    showPayDialog() {
      this.isShowBuyDialog = true;
      console.log(this.isShowBuyDialog);
    },
    closeDialog(value) {
      this.isShowBuyDialog = false;
    }
  }
};
</script>

<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>



