<template>
  <div class="index-wrap">
     <div class="index-left">
        <div class="index-left-block">
          <h2>全部商品</h2>
          <template v-for="product in productList">
              <h3>{{product.title}}</h3>
              <ul>
                 <li v-for="item in product.list">
                   <a :href="item.url" >{{item.name}}</a>
                   <span  v-if="item.hot" class="hot-tag">hot</span>
                 </li>
              </ul>
              <div  class="hr"></div>
          </template>
        </div>

         <div class="index-left-block lastest-news">
          <h2>最新消息</h2>
          <template v-for="news in newsList">
              <ul>
                 <li>
                   <a :href="news.url" target="_blank" class="new-item">{{news.title}}</a>
                 </li>
              </ul>
              <div  class="hr"></div>
          </template>
        </div>
     </div>
     <div class="index-right">
       <slide-show :slides="slides" :inv="slideSpeed"></slide-show>
        <div class="index-board-list">
         <div class="index-board-item"  v-for="(item,index) in boardList" :class="['index-board-'+item.id,{'line-last': index%2 !==0}]"> 
           <div  class="index-board-item-inner"  >
            <h2>{{item.title}}</h2>
            <P>{{item.description}}</P>
            <div class="index-board-button">
              <router-link class="button" :to="{path: 'detail/' + item.toKey}">立即购买</router-link>
            </div>  
           </div>
          </div>
        </div>

     </div>
  </div>
</template>

<script>
import slideShow from "../components/slideShow";

export default {
  components: {
    slideShow
  },
  created() {
    this.$http.get("api/getNewsList").then(
      data => {
        console.log("data", data);
      },
      error => {
        console.log("error", error);
      }
    );
  },
  data() {
    return {
      slideSpeed: 3000,
      slides: [
        {
          src: require("../assets/slideShow/pic1.jpg"),
          title: "xxx1",
          href: "detail/analysis"
        },
        {
          src: require("../assets/slideShow/pic2.jpg"),
          title: "xxx2",
          href: "detail/count"
        },
        {
          src: require("../assets/slideShow/pic3.jpg"),
          title: "xxx3",
          href: "http://xxx.xxx.com"
        },
        {
          src: require("../assets/slideShow/pic4.jpg"),
          title: "xxx4",
          href: "detail/forecast"
        }
      ],
      boardList: [
        {
          title: "开放产品",
          description: "开放产品是一款开放产品",
          id: "car",
          toKey: "analysis",
          saleout: false
        },
        {
          title: "品牌营销",
          description: "品牌营销帮助你的产品更好地找到定位",
          id: "earth",
          toKey: "count",
          saleout: false
        },
        {
          title: "使命必达",
          description: "使命必达快速迭代永远保持最前端的速度",
          id: "loud",
          toKey: "forecast",
          saleout: true
        },
        {
          title: "勇攀高峰",
          description: "帮你勇闯高峰，到达事业的顶峰",
          id: "hill",
          toKey: "publish",
          saleout: false
        }
      ],
      newsList: [
        {
          id: 1,
          title: "新闻条目1新闻条目1新闻条目1新闻条目1",
          url: "http://baidu.com"
        },
        {
          id: 2,
          title: "新闻条目2新闻条目2新闻条目2新闻条目2",
          url: "http://baidu.com"
        },
        {
          id: 3,
          title: "新闻条3新闻条3新闻条3",
          url: "http://baidu.com"
        },
        {
          id: 4,
          title: "新闻条4广告发布",
          url: "http://baidu.com"
        }
      ],
      productList: {
        pc: {
          title: "PC产品",
          list: [
            {
              name: "数据统计",
              url: "/detail/count"
            },
            {
              name: "数据预测",
              url: "/detail/forecast"
            },
            {
              name: "流量分析",
              url: "/detail/analysis",
              hot: true
            },
            {
              name: "广告发布",
              url: "/detail/publish"
            }
          ]
        },
        app: {
          title: "手机应用类",
          last: true,
          list: [
            {
              name: "91助手",
              url: "http://baidu.com"
            },
            {
              name: "产品助手",
              url: "http://baidu.com",
              hot: true
            },
            {
              name: "智能地图",
              url: "http://baidu.com"
            },
            {
              name: "团队语音",
              url: "http://baidu.com"
            }
          ]
        }
      }
    };
  }
};
</script>

<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-right {
  float: left;
  width: 900px;
}

.index-left {
  float: left;
  width: 300px;
  text-align: left;
}

.index-left-block {
  margin: 15px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
}

.index-left-block h2 {
  background: #4fc08d;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}

.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: bold;
  color: #222;
}

.index-left-block ul {
  overflow: hidden;
  padding: 10px 15px;
}
.index-left-block ul li {
  padding: 5px;
}

.hot-tag {
  color: #fff;
  background: red;
}

.lastest-news {
  min-height: 512px;
}

.lastest-news li {
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.index-board-list {
  overflow: hidden;
}
.index-board-item {
  float: left;
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner {
  background: url(../assets/images/car.png) no-repeat;
}
.index-board-loud .index-board-item-inner {
  background: url(../assets/images/loud.png) no-repeat;
}
.index-board-earth .index-board-item-inner {
  background: url(../assets/images/earth.png) no-repeat;
}
.index-board-hill .index-board-item-inner {
  background: url(../assets/images/hill.png) no-repeat;
}

.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}
.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 512px;
}
.hot-tag {
  background: red;
  color: #fff;
}
.new-item {
  display: inline-block;
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



